import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import Card2 from "../Components/Card2";
import VideoMetaData from "../Components/VideoMetaData";
import { getVideoById } from "../redux/actions/videos.action";

const Watch = () => {
  const { id } = useParams();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getVideoById(id));
  }, [dispatch, id]);
  const { loading, video } = useSelector((state) => state.selectedVideo);
  return (
    <>
      <div className="watch-screen">
        <div className="container">
          <div className="video-section">
            <iframe
              className="video-player"
              src={`https://www.youtube.com/embed/${id}`}
              title="YouTube video player"
              frameborder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
            {!loading ? (
              <VideoMetaData video={video} videoId={id} />
            ) : (
              <h1>loading</h1>
            )}
            {/* <div className="video-details">
              <div className="title">Video title will display here</div>
              <div className="bottom">
                <div className="views-time">
                  <div className="views">999B Views</div>
                  <div className="time">11 Month</div>
                </div>
                <div className="like-share">
                  <div className="like">
                    1000K
                    <span
                      className="iconify"
                      data-icon="ant-design:heart-filled"
                    ></span>
                  </div>
                  <div className="share">
                    <span className="iconify" data-icon="fa-solid:share"></span>
                  </div>
                </div>
              </div>
            </div>
            <div className="channel-details">
              <div className="left">
                <div className="icon">
                  <img
                    src="https://yt3.ggpht.com/ytc/AKedOLQbADeaRG5dk_jYMNl8xO5jy6wT96sGKNe0lB99zw=s88-c-k-c0x00ffffff-no-rj"
                    alt="youtube channel icon"
                  />
                </div>
                <div className="sub-details">
                  <div className="name">Channel name here</div>
                  <div className="subs-count">1.0K subscriber</div>
                </div>
              </div>
              <button className="subs-btn">subscribe</button>
            </div> */}
          </div>
          <div className="other-video-section">
            <Card2 />
            <Card2 />
            <Card2 />
            <Card2 />
            <Card2 />
            <Card2 />
          </div>
        </div>
      </div>
    </>
  );
};

export default Watch;
