import React from "react";
import numeral from "numeral";
import moment from "moment";

const VideoMetaData = ({ video, videoId }) => {
  return video ? (
    <>
      <div className="video-details">
        <div className="title">{video?.snippet?.title}</div>
        <div className="bottom">
          <div className="views-time">
            <div className="views">
              {numeral(video?.statistics?.viewCount).format("0.a")} Views
            </div>
            <div className="time">
              {moment(video?.snippet?.publishedAt).fromNow()}
            </div>
          </div>
          <div className="like-share">
            <div className="like">
              {numeral(video?.statistics?.likeCount).format("0.a")}
              <span
                className="iconify"
                data-icon="ant-design:heart-filled"
              ></span>
            </div>
            <div className="share">
              <span className="iconify" data-icon="fa-solid:share"></span>
            </div>
          </div>
        </div>
      </div>
      <div className="channel-details">
        <div className="left">
          <div className="icon">
            <img
              src="https://yt3.ggpht.com/ytc/AKedOLQbADeaRG5dk_jYMNl8xO5jy6wT96sGKNe0lB99zw=s88-c-k-c0x00ffffff-no-rj"
              alt="youtube channel icon"
            />
          </div>
          <div className="sub-details">
            <div className="name">Channel name here</div>
            <div className="subs-count">1.0K subscriber</div>
          </div>
        </div>
        <button className="subs-btn">subscribe</button>
      </div>
    </>
  ) : (
    "Loading..."
  );
};

export default VideoMetaData;
