import React from "react";
import { useSelector } from "react-redux";

const Navbar = () => {
  const { photoUrl } = useSelector((state) => state?.auth?.user);

  return (
    <nav className="navbar">
      <div className="logo">UTUBE</div>
      <div className="search">
        <input type="text" placeholder="Search for videos, channel, etc" />
        <button type="submit">
          <i className="iconify" data-icon="fluent:search-20-filled"></i>
        </button>
      </div>
      <div className="user">
        <div className="profile-img">
          <img src={photoUrl ? photoUrl : null} alt="user profile" />
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
