import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { logout } from "../redux/actions/auth.action";

const SideBar = () => {
  const dispatch = useDispatch();
  const log_out = () => {
    dispatch(logout());
  };

  return (
    <div className="sidebar">
      <div className="uls">
        <ul className="main-links">
          <Link to="/" className="link active">
            <span className="iconify" data-icon="ant-design:home-filled"></span>
            Home
          </Link>
          <Link to="/" className="link">
            <span className="iconify" data-icon="zondicons:explore"></span>
            Explore
          </Link>
          <Link to="/" className="link">
            <span
              className="iconify"
              data-icon="eos-icons:subscriptions-created"
            ></span>
            Subscriptions
          </Link>
        </ul>
        <ul className="second-links">
          <Link to="/" className="link">
            <span className="iconify" data-icon="ic:round-video-library"></span>
            Library
          </Link>
          <Link to="/" className="link">
            <span className="iconify" data-icon="fontisto:history"></span>
            History
          </Link>
          <Link to="/" className="link">
            <span
              className="iconify"
              data-icon="ic:baseline-watch-later"
            ></span>
            Watch Later
          </Link>
          <Link to="/" className="link">
            <span
              className="iconify"
              data-icon="ant-design:heart-filled"
            ></span>
            Liked Videos
          </Link>
        </ul>
        <ul className="other-links">
          <li className="link" onClick={() => log_out()}>
            <span className="iconify" data-icon="tabler:logout"></span>
            Logout
          </li>
        </ul>
      </div>
    </div>
  );
};

export default SideBar;
