import {
  LOAD_PROFILE,
  LOGIN_FAIL,
  LOGIN_OUT,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
} from "../actionTypes";

const initialState = {
  accessToken: sessionStorage.getItem("accessToken")
    ? sessionStorage.getItem("accessToken")
    : null,
  user: sessionStorage.getItem("user")
    ? JSON.parse(sessionStorage.getItem("user"))
    : {
        photoUrl: null,
      },
  loading: false,
};

export const authReducer = (prevState = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case LOGIN_REQUEST:
      return {
        ...prevState,
        loading: true,
      };
    case LOGIN_SUCCESS:
      return {
        ...prevState,
        accessToken: payload,
      };
    case LOAD_PROFILE:
      return {
        ...prevState,
        user: payload,
      };
    case LOGIN_FAIL:
      return {
        ...prevState,
        error: payload,
      };
    case LOGIN_OUT:
      return {
        ...prevState,
        accessToken: null,
        user: null,
      };
    default:
      return prevState;
  }
};
