import React, { useEffect, useState } from "react";
import moment from "moment";
import numeral from "numeral";
import axios from "../Api/connect";
import { Link } from "react-router-dom";

const Card = ({ video }) => {
  const [views, setViews] = useState(null);
  const [channelIcon, setChannelIcon] = useState(null);
  const [vidduration, setDuration] = useState(null);
  const seconds = moment.duration(vidduration).asSeconds();
  const _duration = moment.utc(seconds * 1000).format("mm:ss");

  const {
    id,
    contentDetails: { duration },
    snippet: {
      channelTitle,
      channelId,
      title,
      publishedAt,
      thumbnails: { high },
    },
    statistics: { viewCount },
  } = video;

  useEffect(() => {
    const fetchChannel = async () => {
      const {
        data: { items },
      } = await axios("/channels/", {
        params: {
          part: "snippet",
          id: channelId,
        },
      });
      setViews(viewCount);
      setDuration(duration);
      setChannelIcon(items[0].snippet.thumbnails.default.url);
    };
    fetchChannel();
  }, [channelId, viewCount]);

  return (
    <Link to={`/watch/${id}`} className="card">
      <div className="card-img">
        <img src={high.url} alt="youtube video" />
        <div className="duration">{_duration}</div>
      </div>
      <div className="card-body">
        <div className="heading">{title.slice(0, 50)}</div>
        <div className="sub-details">
          <div className="profile-img">
            <img src={channelIcon} alt="youtube profile" />
          </div>
          <div className="user-details">
            <div className="name">{channelTitle}</div>
            <div className="video-details">
              <div className="video-views">
                {numeral(views).format("0.a")} views
              </div>
              <div className="video-time">{moment(publishedAt).fromNow()}</div>
            </div>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default Card;
